.card {
    position: relative;
    overflow: hidden;
}

.card img {
    width: 100%;
    height: 200px;
    object-fit: cover; 
    display: block;
    border-radius: 6px;
}

/*front of the card*/
.card .front{
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .front{
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/*back of the card*/
.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s; 
}
.flipped .back{
    transform: rotateY(90deg);
    transition-delay: 0s;
}
